.user-dashboard-container {
  max-width: 1000px;
  margin: 2rem auto;
  padding: 2rem;
  background-color: var(--white);
  border-radius: var(--radius-lg);
  box-shadow: var(--box-shadow);
  border: 1px solid var(--border-color);
}

h1 {
  font-size: 2.5rem;
  color: var(--text-primary);
  margin-bottom: 2rem;
  text-align: center;
}

/* Filters Section */
.filters-section {
  display: flex;
  gap: 1rem;
  margin-bottom: 2rem;
  flex-wrap: wrap;
}

.search-box {
  flex: 1;
  min-width: 250px;
}

.search-input {
  width: 100%;
  padding: 0.75rem 1rem;
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  font-size: 1rem;
  transition: border-color var(--transition-base);
}

.search-input:focus {
  outline: none;
  border-color: var(--primary-color);
}

.filter-box {
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.filter-box label {
  font-weight: 600;
  color: var(--text-primary);
  white-space: nowrap;
}

.filter-select {
  padding: 0.75rem 1rem;
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  font-size: 1rem;
  background-color: var(--white);
  cursor: pointer;
  transition: border-color var(--transition-base);
}

.filter-select:focus {
  outline: none;
  border-color: var(--primary-color);
}

.loading-message,
.error-message,
.no-courses-message {
  text-align: center;
  padding: 1.5rem;
  font-size: 1.1rem;
  color: var(--text-secondary);
}

.error-message {
  color: var(--danger-color);
}

.course-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.course-card {
  background-color: var(--background-secondary);
  padding: 1.5rem;
  border-radius: var(--radius-md);
  border: 1px solid var(--border-color);
  box-shadow: var(--box-shadow-sm);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: transform 0.2s ease-in-out;
}

.course-card:hover {
  transform: translateY(-5px);
}

.course-card h3 {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin-top: 0;
  margin-bottom: 0.75rem;
}

.course-description {
  font-size: 0.95rem;
  color: var(--text-secondary);
  line-height: 1.5;
  margin-bottom: 1rem;
  flex-grow: 1;
}

.course-meta {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

.course-score {
  font-weight: 600;
  color: var(--primary-color);
}

.course-actions {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.progress-hint {
  font-size: 0.875rem;
  color: var(--warning-color);
  font-style: italic;
  text-align: center;
}

.completed-message {
  background-color: var(--success-light);
  color: var(--success-color);
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-md);
  text-align: center;
  font-weight: 600;
  font-size: 1rem;
  border: 2px solid var(--success-color);
}

.btn-primary {
  background-color: var(--primary-color);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-md);
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
  transition: background-color var(--transition-base);
  width: 100%;
}

.btn-primary:hover {
  background-color: var(--primary-hover);
}

/* Badge styles (reused from admin components) */
.badge {
  display: inline-flex;
  align-items: center;
  padding: 0.375rem 0.875rem;
  border-radius: 9999px;
  font-size: 0.8125rem;
  font-weight: 600;
  line-height: 1;
  text-transform: capitalize;
}

.badge-info {
  background: var(--info-light);
  color: var(--info-color);
}

.badge-warning {
  background: var(--warning-light);
  color: var(--warning-color);
}

.badge-success {
  background: var(--success-light);
  color: var(--success-color);
}

.badge-default {
  background: var(--background-secondary);
  color: var(--text-secondary);
  border: 1px solid var(--border-color);
}

/* === New Styles for Sections === */
.course-section {
  margin-bottom: 3rem;
}

.course-section h2 {
  font-size: 1.5rem;
  color: var(--text-primary);
  margin-bottom: 1.5rem;
  padding-bottom: 0.5rem;
  border-bottom: 2px solid var(--border-color);
}

.empty-section {
  color: var(--text-secondary);
  font-style: italic;
  padding: 1rem;
}

/* Card Header adjustments */
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: start;
  margin-bottom: 0.75rem;
}

.card-header h3 {
  margin: 0;
  font-size: 1.25rem;
  padding-right: 0.5rem;
}

/* Badges & Meta */
.date-badge,
.capacity-badge {
  display: inline-flex;
  align-items: center;
  font-size: 0.875rem;
  color: var(--text-secondary);
  background: var(--background-primary);
  padding: 0.25rem 0.5rem;
  border-radius: var(--radius-sm);
  border: 1px solid var(--border-color);
}

.capacity-badge.full {
  color: var(--danger-color);
  border-color: var(--danger-color);
  background: var(--danger-light);
  font-weight: 600;
}

/* Button Variants */
.btn-success {
  background-color: var(--success-color);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-md);
  cursor: pointer;
  font-weight: 600;
  font-size: 1rem;
  transition: background-color var(--transition-base);
  width: 100%;
}

.btn-success:hover {
  background-color: #059669;
}

.btn-disabled {
  background-color: var(--text-muted);
  color: white;
  border: none;
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius-md);
  cursor: not-allowed;
  font-weight: 600;
  font-size: 1rem;
  width: 100%;
  opacity: 0.7;
}

/* Specific Card Tweaks */
.history-card {
  opacity: 0.9;
  border-left: 4px solid var(--success-color);
}

.active-card {
  border-left: 4px solid var(--info-color);
}

.available-card {
  border-left: 4px solid var(--border-color);
}

/* Exam History Section */
.exam-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 1.5rem;
}

.exam-card {
  background-color: var(--white);
  border: 1px solid var(--border-color);
  border-radius: var(--radius-md);
  padding: 1.5rem;
  box-shadow: var(--box-shadow-sm);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border-left: 4px solid var(--border-color);
  transition: transform 0.2s;
}

.exam-card.passed {
  border-left-color: var(--success-color);
}

.exam-card.failed {
  border-left-color: var(--danger-color);
}

.exam-card:hover {
  transform: translateY(-3px);
}

.exam-header {
  margin-bottom: 1rem;
}

.exam-header h3 {
  font-size: 1.1rem;
  margin: 0 0 0.5rem 0;
  color: var(--text-primary);
}

.exam-date {
  font-size: 0.85rem;
  color: var(--text-secondary);
}

.exam-result {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 1rem;
  padding-top: 1rem;
  border-top: 1px solid var(--border-color);
}

.exam-score {
  font-size: 1.5rem;
  font-weight: 700;
  color: var(--text-primary);
}

.exam-card.passed .exam-score {
  color: var(--success-color);
}

.exam-card.failed .exam-score {
  color: var(--danger-color);
}

.result-icon {
  font-size: 1.5rem;
  font-weight: bold;
}

.exam-card.passed .result-icon {
  color: var(--success-color);
}

.exam-card.failed .result-icon {
  color: var(--danger-color);
}